*{
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  color:black;
  font-family: "Allerta";
 
  
  
}
body{
  background: #e1ecf2;
}
.wrapper{
  margin: 10px;
}
.wrapper .top_navbar{
  width: calc(100% - 20px);
  height: 60px;
  display: flex;
  position: fixed;
  top: 10px;
}

.wrapper .top_navbar .logo{
  width: 70px;
  height: 100%;
  background: #F2F5F6;
  padding: 15px 17px;
  border-top-left-radius: 20px;
  cursor: pointer;
}

.wrapper .top_navbar .logo div{
  width: 35px;
  height: 4px;
  background: #92a6e2;
  margin: 5px 0;
  
}

.wrapper .top_navbar .top_menu{
  width: calc(100% - 70px);
  height: 100%;
  background: #F2F5F6;
  border-top-right-radius: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  
}

.wrapper .top_navbar .top_menu .logo{
  color:#F2F5F6;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 3px;
}

.wrapper .top_navbar .top_menu ul{
  display: flex;
}

.wrapper .top_navbar .top_menu ul li a{
  display: block;
  margin: 0 10px;
  width: 35px;
  height: 35px;
  line-height: 35px;
  text-align: left;
  border: 1px solid #F2F5F6;
  
}

.wrapper .top_navbar .top_menu ul li a:hover{
  background: #F2F5F6;
  color: #fff;
}

.wrapper .top_navbar .top_menu ul li a:hover i{
  color: #fff;
}

.wrapper .sidebar{
  position: fixed;
  top: 0px;
  left: 0px;
  background: #F2F5F6;
  width: 155px;
  height: 100%;
  border-bottom-left-radius: 20px;
  color: black;
 
}
.sidebar 
{
  opacity: 0.8;
}

.wrapper .sidebar ul li a{
    display: block;
    padding: 20px;
    color: #fff;
    
    position: relative;
    margin-bottom: 1px;
    color: #92a6e2;
    white-space: nowrap;
}

.wrapper .sidebar ul li a:before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background: #92a6e2;
  
  display: none;
}

.wrapper .sidebar ul li a span.icon{
  margin-right: 10px;
  display: inline-block;
 
}

.wrapper .sidebar ul li a span.title{
  display: inline-block;
  
}

.wrapper .sidebar ul li a:hover,
.wrapper .sidebar ul li a.active{
  background: #3d3cbf;  
  color: #ffffff;
  
 
}

.wrapper .sidebar ul li a:hover:before,
.wrapper .sidebar ul li a.active:before{
  display: block;
  
}

.wrapper .main_container{
  width: (100% - 200px);
  margin-top: 70px;
  margin-left: 200px;
  padding: 15px;
 
}

.wrapper .main_container .item{
  background: #fff;
  margin-bottom: 10px;
  padding: 15px;
  font-size: 14px;
  line-height: 22px;
}




